<template>
	<div>
		<div class="item" v-for="item in list">
			<div class="left">
				<img src="../../../static/evaluate.png" />

			</div>
			<div class="right">
				<div class="top">
					<div style="display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-align:center;-moz-box-align:center;-ms-flex-align:center;-webkit-align-items:center;align-items:center;">
						<span style="float: left;margin-right: 0.1rem;">{{item.name}}</span>
						<div v-if="item.type=='question'" class="sign">发问</div>
						<div v-if="item.type=='answer'" class="sign" style="background: rgb(29, 172, 249);">回复</div>
					</div>
					<div style="font-size: 0.24rem;color: #999999;">
						{{item.time}}
					</div>
				</div>
				<div class="bottom">
					{{item.content}}
				</div>
			</div>
		</div>
	

	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: this.$route.params.CommunicateId,
				list: [],
				answerList: [],
				questionList: []

			}

		},
		mounted() {

			this.getData()

		},
		methods: {
			getData(id) {
				var obj = {
					"s": this.$store.state.sessionId,
					"id": this.id

				}
				this.$Api.LeaveWordDetails(obj).then((res) => {
					
					
					for(var i=0;i<res.q.leaveword.length;i++){
						
						
						if(i%2==0){
							var obj={
							id:res.q.leaveword[i].id,
							content:res.q.leaveword[i].content,
							name:res.q.leaveword[i].name,
							time:res.q.leaveword[i].time,
							type:"question"
						}
							
							
						}else{
							var obj={
							id:res.q.leaveword[i].id,
							content:res.q.leaveword[i].content,
							name:res.q.leaveword[i].name,
							time:res.q.leaveword[i].time,
							type:"answer"
						
						}
							
						}
						
						
						
						
						this.list.push(obj)
						
//						if(i%2==0){
//							this.questionList.push(this.list[i])
//							
//							
//						}else{
//							
//						this.answerList.push(this.list[i])	
//						}
						
						
						
					}
					

				})

			}
		}

	}
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
	.item {
		width: 100%;
		display: flex;
		flex-direction: row;
		flex-wrap: nowrap;
		border-bottom: 0.02rem solid #CCCCCC;
		padding-top: 0.3rem;
		box-sizing: border-box;
		.left {
			width: 1.4rem;
			text-align: center;
			img {
				width: 0.95rem;
				height: 0.95rem;
			}
		}
		.right {
			width: 6.1rem;
			padding-right: 0.3rem;
			box-sizing: border-box;
			.top {
				height: 0.35rem;
				display: flex;
				font-size: 0.3rem;
				justify-content: space-between;
				align-items: baseline;
				.sign {
					float: left;
					width: 0.75rem;
					height: 0.3rem;
					background: #fa8a12;
					text-align: center;
					color: #FFFFFF;
					font-size: 0.24rem;
					border-radius: 0.3rem;
				}
			}
			.bottom {
				margin-top: 0.3rem;
				font-size: 0.3rem;
				padding-bottom: 0.2rem;
			}
		}
	}
</style>


